header {
  color: rgb(0, 0, 0);
  position: relative;
  height: 1000px;
  min-height: 500px;
  width: 100%;
  background: #161415 url(../images/background-2.jpg) no-repeat top center;
  background-size: cover !important;
  -webkit-background-size: cover !important;
  text-align: center;
  overflow: hidden;
}
header .social {
  margin: 24px 0;
  padding: 0;
  font-size: 3px;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.8);
}
header .social li {
  display: inline-block;
  margin: 0 15px;
  padding: 0;
}
header .social li a {
  color: rgb(197, 169, 153);
}
header .banner-text h3 {
  font-size: 18px/1.9em;
  color: #000000;
  margin: 0 auto;
  width: 70%;
}
.avatar {
  position: relative;
  max-height: 30vw;
  min-height: 200px;
  width: auto;
  border-radius: 50%;
  border: 2px solid rgb(197, 169, 153);
  padding: 20px;
  /* top: 3em; */
  margin: 3em 0;
}
#nav-wrap {
  z-index: 100;
  font: 12px;
  background: transparent !important;
  letter-spacing: 1.5px;
  width: auto;
  position: fixed;
  top: 0;
  right: 0;
}
#nav-wrap > a {
  width: 48px;
  height: 48px;
  text-align: left;
  background-color: rgb(197, 169, 153);
  position: relative;
  border-left: 2px solid rgba(255, 255, 255, 0.5);
  border-bottom: 2px solid rgba(255, 255, 255, 0.5);
  float: right;

  font: 0/0 a;
  text-shadow: none;
  color: transparent;

  position: relative;
  top: 0px;
  /* right: 30px; */
}

#nav-wrap > a:before,
#nav-wrap > a:after {
  position: absolute;
  border: 2px solid #fff;
  top: 35%;
  left: 25%;
  right: 25%;
  content: "";
}
#nav-wrap > a:after {
  top: 60%;
}

#nav-wrap:not(:target) > a:first-of-type,
#nav-wrap:target > a:last-of-type {
  display: block;
}

#nav-wrap ul#nav {
  height: auto;
  display: none;
  clear: both;
  width: auto;
  float: right;

  position: relative;
  /* top: 12px; */
  right: 0;
}
#nav-wrap > a.mobile-btn {
  display: none;
}

#nav-wrap:target > ul#nav {
  display: block;
  padding: 30px 20px 48px 20px;
  background: rgba(0, 0, 0, 0.7);
  margin: 0;
  clear: both;
}

ul#nav li {
  display: block;
  height: auto;
  margin: 0 auto;
  padding: 0 4%;
  text-align: left;
  border-bottom: 1px solid rgb(197, 169, 153);
  border-bottom-style: dotted;
}

ul#nav li a {
  display: block;
  margin: 0;
  padding: 0;
  margin: 12px 0;
  line-height: 16px;
  border: none;
}
ul#nav li a:visited {
  color: white;
}
ul#nav li a {
  color: white;
  text-decoration: none;
}
header .banner {
  padding-bottom: 12px;
  padding-top: 6px;
}
header .banner-text h1 {
  letter-spacing: 4px;
  font-weight: lighter;
  font-family: "Bebas Neue", cursive;
  color: rgb(197, 169, 153);
  font-size: 78px;
}
header .banner-text h3 {
  margin: 0 auto;
  color: rgb(221, 221, 221);
  font-size: 22px;
  font-weight: lighter;
  letter-spacing: 1px;
  width: 85%;
}
header .banner-text hr {
  width: 80%;
  margin: 18px auto;
}

header .social {
  /* margin: 18px 0 24px 0; */
  font-size: 24px;
  line-height: 36px;
}
header .social li {
  border: 1px solid white;
  border-radius: 50%;
  padding: 6px 12px;

  margin: 0 8px;
}
header .social li:hover {
  cursor: pointer;
}

header .scrolldown {
  display: none;
}
